  const imageHeightPixels = imgEl.clientHeight;
  let hotspotWidthPercent: number, hotspotHeightPercent: number;
  const size = 0.3;
  const smallestDim = imageWidthPixels > imageHeightPixels ? 'height' : 'width';
  if (smallestDim === 'height') {
